<template>
    <!--地址页内容-->
    <div class="_address d-flex justify-content-center align-items-start ">
        <div id="_address-box" class="_address-box card card-body px-0 mx-0 ">
            <!--地址信息标题栏-->
            <div class="d-flex justify-content-between pb-2 pb-sm-3">
                <div class="txt-subtitle txt-subtitle-space  px-2 px-sm-3 " style="font-weight:bold" @click="$router.go(-1)">
                    <i class="iconfont icon-left" style="font-size:22px;color:#1f1f1f"></i>
                    <span>我的收货地址</span>
                </div>
                <!-- #E8DFE2,#F1E7DE-->
                <div id="_manageBtn" class="txt-mini px-2 px-sm-3" @click="changeDeleteAddress">
                    <div class="  _txt btn px-3 py-1 text-muted">管理</div>
                    <div class="  _btn btn rounded rounded-pill px-2 py-1 ">管理&nbsp;X</div>
                </div>
            </div>
            <!--地址信息栏-->
            <div class="_addressContent">
                <div class="_shopItemBox bg-white py-3 " v-for="(item,index) in list" :key="index" :class="{active:defaultId===item.id}">
                    <div class="_shopItem  row d-flex align-items-center" >
                        <div class="col-10 d-flex flex-column justify-content-start align-items-start px-4 pb-2">
                            <div class="py-1">
                                <span class="txt-body" style="font-weight: bold">{{item.name}}</span>
                                <span class="txt-subbody text-muted px-1">{{item.tel}}</span>
                                <div class="_defaultShow txt-subbody  d-inline rounded rounded-lg p-1 mx-1" style="border:1px solid #ECA31C;color:#ECA31C;">默认</div>
                            </div>
                            <div class="text-truncate w-100 d-flex justify-content-start"  >
                                <span>{{item.address}} </span>
                            </div>
                        </div>
                        <div class="col-2">
                            <i class="iconfont icon-edit" style="font-size:20px;color:#ECA31C" @click="editAddress(item.id)"></i>
                        </div>
                    </div>
                    <div class="_changeMessage row txt-mini text-muted  pt-2 "
                         style="border-top:1px solid #E1DEE1">
                        <div class="_defaultBox col-10 px-4 d-flex justify-content-between align-items-center "
                             @click="setDefault(index,item.id)">
                            <div class="_defaultBtn d-flex align-items-center">
                                <i class="iconfont icon-gouhao _check" ></i>
                                <i class="iconfont icon-icon _empty" ></i>
                                <span class="px-1">设置默认地址</span>
                            </div>
                        </div>
                        <div class="col-2 px-2 d-flex justify-content-center align-items-center">
                            <span  @click="setDeleteAddressPre(item.id)">删除</span>
                        </div>
                    </div>
                </div>
            </div>
            <!--地址信息栏，数据不存在，提示信息-->
            <div class="d-flex flex-column justify-content-start align-items-center" style="height:400px;" v-if="!list.length">
                <img src="~assets/images/pic/bg-address.png" alt="" class="img-responsive " style="max-width:400px;">
                <span class="px-1 text-muted px-2"  >暂无收货地址</span>
            </div>
            <!--地址信息添加栏-->
            <div class="_totalBalance py-1 py-sm-2  " >
                <div class=" d-flex justify-content-center">
                    <div class="txt-body rounded rounded-pill d-flex align-items-center justify-content-center text-white py-2 w-75"
                         style="background:#ECA31C" @click="addAddress">
                        <i class="iconfont icon-add-bold" style="font-size:18px;color:white"></i>
                        <span class="px-1">添加收货地址</span>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <!--地址删除提示栏-->
    <el-dialog
            title="提示"
            :modelValue="dialogVisible"
            width="30%"
            :before-close="handleClose">
        <span class="txt-message">请确认是否删除当前的地址</span>
        <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="onDeletePre(true)">确 定</el-button>
                <el-button @click="onDeletePre(false)">取 消</el-button>
            </span>
    </el-dialog>
</template>
<script>
    import $ from 'jquery';
    import {ElLoading,ElMessage} from 'element-plus';
    import {getAddressList, deleteAddress,getAddressDetail,editAddress,setDefaultAddress} from 'network/address';

    import {useRoute, useRouter} from "vue-router";
    import {useStore} from "vuex";
    import {ref, reactive, toRefs, onMounted} from "vue";

    export default {
        name: "Address",
        setup() {
            const route = useRoute();
            const router = useRouter();
            const store = useStore();

            const state = reactive({
                list: [],                       // 地址列表数据
                defaultId:0                     // 默认的地址id
            });
            let haveData = ref(true);           // 默认有数据，即不显示提示的文字
            let dialogVisible = ref(false);     // 删除弹框
            let deleteId = ref(0);              // 删除的地址id
            let positionPre = ref(0);           // 鼠标滚动前的位置

            onMounted(() => {
                let loadingInstance = ElLoading.service({ fullscreen: false,text:'加载页面中。。。',target: '._address-box'});
                initAddress();
                if(loadingInstance!==null){
                    loadingInstance.close();                // 页面加载中...
                    loadingInstance = null;
                }
            });


            // 监听窗口滚动事件， 自动执行
            $(window).scroll(function () {
                if (positionPre.value >= $(window).scrollTop()) {     // 上滑
                    $('._totalBalance').css({'position':'fixed', 'bottom': '0','width':'100%'});
                } else {                                              // 下滑
                    $('._totalBalance').css('position', 'relative');
                }
                positionPre.value = $(window).scrollTop()
            });


            // 初始化地址列表数据
            const initAddress = ()=>{
                getAddressList().then(res => {
                    if (res.data.length === 0) {
                        state.list = [];
                        haveData = false;
                        return;
                    }
                    state.list = res.data.map(item => {          // 参数必需符合地址列表格式
                        if(item.is_default){
                            state.defaultId = item.id;
                            store.commit('setAddressId',item.id)      // 给vuex提交地址的ID
                        }
                        return {
                            id: item.id,
                            name: item.name,
                            tel: item.phone,
                            address: `${item.province} ${item.city} ${item.county} ${item.address}`,
                            isDefault: item.is_default
                        }
                    });
                    haveData = true;
                    console.log("address-data",res);
                }).catch(err=>{
                    console.log("address-err",err);
                    ElMessage.error('请求数据失败！');
                });
            };

            const addAddress = () => {
                router.push({path: "/addressedit", query: {type: "add"}});
            };
            const editAddress = (id) => {
                router.push({path: "/addressedit", query: {type: "edit", addressId: id}})
            };


            // 点击删除地址后的预处理
            const setDeleteAddressPre = (id) =>{
                dialogVisible.value = true;
                deleteId.value=id;
            };
            // 删除前的预处理
            const onDeletePre = (flag) =>{
                dialogVisible.value = false;
                console.log('当前需要删除的地址id是 ',deleteId.value);
                if(flag){
                    setDeleteAddress(deleteId.value);
                }
            };

            // 确认是否删除的弹框
            const handleClose = (done) => {
                dialogVisible.value = false;
                done();
            };

            // 删除地址, 只有在编辑状态下才能删除
            const setDeleteAddress = (id) =>{

                deleteAddress(id).then(res => {
                    ElMessage.success('删除地址成功！');
                    if(store.getters.getAddressId === id)           // 如果删除的是默认数据，则确认把默认值设置为空
                        store.commit('setAddressId','');            // 决定性属性，设置为空就可以
                }).catch(err=>{
                    console.log('address-delete-err',err);
                    ElMessage.error('删除地址失败！');
                });
                setTimeout(()=>{              // 等待后台更新数据
                    //init();        // 刷新列表
                    state.list.forEach((item,index) => {          // 参数必需符合地址列表格式
                        if(item.id===id){                         //删除一个元素
                            state.list.splice(index,1)
                        }
                    });
                },500);
            };

            // 把数据保存在vuex
            const saveStateMessage = (id)=>{
                state.list.forEach((item)=>{
                    if(item.id === id){
                        store.commit('setAddressId',item.id);          // 先设置为0
                        store.commit('setAddressUserName',item.name);
                        store.commit('setAddressUserPhone',item.tel);
                        var tt_message = item.address.split(' ');
                        //console.log("地址数据是",tt_message[0],tt_message[1],tt_message[2],tt_message[3]);
                        store.commit('setAddressUserProvince',tt_message[0]);
                        store.commit('setAddressUserCity',tt_message[1]);
                        store.commit('setAddressUserCounty',tt_message[2]);
                        store.commit('setAddressUserDetail',tt_message[3]);
                    }
                });
            };


            // 设置默认地址
            const onSetDefault = (id) =>{
                setDefaultAddress(id).then(res=>{
                    if(res.status === 204){
                        ElMessage.success('设置默认地址成功');
                        saveStateMessage(id);                   // 只有后台数据更新，才会更新浏览器中的数据
                    }
                }).catch(err=>{
                    console.log("address-setdefault-err",err);
                    ElMessage.success('设置默认地址失败')
                })
            };

            // 管理按钮切换删除和添加地址功能
            const changeDeleteAddress = () =>{
                var delNode = $("._changeMessage");
                var changeNode = $("#_manageBtn");
                if (delNode.css('display') === 'none') {
                    delNode.css('display', 'flex');
                    changeNode.children('div._txt').css('display', 'none');
                    changeNode.children('div._btn').css('display', 'block');
                } else {
                    delNode.css('display', 'none');
                    changeNode.children('div._txt').css('display', 'block');
                    changeNode.children('div._btn').css('display', 'none');
                }
            };

            // 设置默认地址
            const setDefault = (index,id) =>{
                var node  = $('#_address-box ._shopItemBox:eq('+index+') ');
                if(node.hasClass('active'))
                    return;
                node.siblings('div').removeClass('active');
                node.addClass('active');
                onSetDefault(id);
            };

            return {...toRefs(state), haveData,changeDeleteAddress, addAddress,
                setDefault, editAddress, onDeletePre, handleClose, setDeleteAddressPre, dialogVisible}
        }
    }
</script>
<style  lang="scss">

    .el-dialog__close{
        font-size:25px!important;
    }
    .el-dialog__body{
        display:flex;
        flex-direction: column;
        justify-content:space-around;
        .txt-message{
            font-size:16px;
            text-align:left;
        }
        .dialog-footer{
            padding-top:15px;
            text-align:right;
        }
    }
    ._address {
        min-height: 600px;

        #_address-box {
            position: relative;
            max-width: 1200px;
            min-height:580px;
            border: 1px solid red;/*前面的值是在相反的位置，即以前面的值向left top 左上渐变*/
            background: linear-gradient(to left top, #F1E4EE, #F9F4EF);

            ._shopItemBox {
                border-radius: 10px;
                border: 3px solid #F3E3DE;
                width: 99%;
                margin: auto;
                position: relative;
            }
            ._changeMessage{
                display:none;
            }
            ._totalBalance {
                position: relative;
                left:0;
                right:0;
                background:white;
                border-top: 3px solid #E1DEE1;
                z-index: 90;
            }
            /*使用样式来操作，而不是选择更换图标*/
            div._shopItemBox div._shopItem  div._defaultShow{
                display:none!important;
            }
            div._shopItemBox div._changeMessage  div._defaultBox div._defaultBtn {
                i.iconfont._empty{
                    display:inline!important;
                }
                i.iconfont._check{
                    display:none!important;
                }
            }
            div._shopItemBox.active div._shopItem  div._defaultShow{
                display:inline!important;
            }
            div._shopItemBox.active div._changeMessage  div._defaultBox div._defaultBtn {
                i.iconfont._empty{
                    display:none!important;
                }
                i.iconfont._check{
                    display:inline!important;
                }
            }



            #_manageBtn div._btn {
                color: #E79989;
                border: 1px solid #E79989;
                font-weight: bold;
                display: none;
            }
        }

        .txt-subtitle-space {
            letter-spacing: 1.5px;
        }

        .txt-body-space {
            letter-spacing: 1px;
        }
    }
</style>
